import React, { Component } from 'react'
import { connect } from 'react-redux'
import List from './component/List'
import Recommend from './component/Recommend'
import Topic from './component/Topic'
import Writer from './component/Writer'
import { actionCreators } from './store/'
import {
  HomeWrapper,
  HomeLeft,
  HomeRight,
  ScrollTop
} from './style'
class Home extends Component {
  render() {
    const { backTopVisible } = this.props;
    return (
      <div>
        <HomeWrapper>
          <HomeLeft>
            <img src="//upload.jianshu.io/admin_banners/web_images/4318/60781ff21df1d1b03f5f8459e4a1983c009175a5.jpg?imageMogr2/auto-orient/strip|imageView2/1/w/1250/h/540"
              className="banner-img"
              alt="banner-img" />
            <Topic />
            <List />
          </HomeLeft>
          <HomeRight>
            <Recommend />
            <Writer />
          </HomeRight>
        </HomeWrapper>
        { backTopVisible ?
          <ScrollTop onClick={this.backTop}>
            <i className="iconfont icon-huidaodingbu backtop"></i>
          </ScrollTop> : null}
      </div>
    )
  }
  // 回到顶部
  backTop() {
    /* console.log(document.documentElement.scrollTop); */
    window.scrollTo(0, 0)
  }
  componentDidMount() {
    //获取数据
    const { getHomePageData } = this.props;
    getHomePageData();
  }
}
const mapState = (state) => ({
  backTopVisible: state.getIn(['home', 'backTopVisible'])
})
const mapDispatch = (dispatch) => ({
  getHomePageData() {
    dispatch(actionCreators.getHomeInfo())
  }
})
export default connect(mapState, mapDispatch)(Home);