import React, { Component } from 'react'
import { connect } from 'react-redux'
import { CSSTransition } from 'react-transition-group';
import { actionCreators } from './store'
import { actionCreators as loginActionCreators } from '../../pages/login/store'
import { Link } from 'react-router-dom'
import {
  HeaderWrapper,
  Logo,
  Nav,
  NavItem,
  SearchWrapper,
  NavSearch,
  SearchInfo,
  SearchInfoTitle,
  SearchInfoSwitch,
  SearchInfoList,
  SearchInfoItem,
  Addition,
  Button
} from './new-style'
class Header extends Component {
  getListArea = () => {
    const { focused, list, page, totalPage, mouseIn, handleMouseEnter, handleMouseLeave, handleChangePage } = this.props;
    const newList = list.toJS();
    const pageList = [];
    if (newList.length) {
      for (let i = (page - 1) * 10; i < page * 10; i++) {
        pageList.push(
          <SearchInfoItem key={newList[i]}>{newList[i]}</SearchInfoItem>
        )
      }
    }
    if (focused || mouseIn) {
      return (
        <SearchInfo
          onMouseEnter={handleMouseEnter}
          onMouseLeave={handleMouseLeave}
        >
          <SearchInfoTitle>热门搜索
          <SearchInfoSwitch
              onClick={() => handleChangePage(page, totalPage, this.spinIcon)}>
              <i ref={(icon) => { this.spinIcon = icon }} className='iconfont icon-spinner6 spin'></i>
            换一批
             </SearchInfoSwitch>
          </SearchInfoTitle>

          <SearchInfoList>
            {pageList}

          </SearchInfoList>
        </SearchInfo>
      )
    } else {
      return null;
    }
  }
  render() {
    const {
      loginStatus,
      focused,
      handleInputFocus,
      handleInputBlur,
      list,
      logout
    } = this.props
    return (
      <HeaderWrapper>
        <Link to="/">
          <Logo />
        </Link>
        <Nav>
          <Link to="/">
            <NavItem className='left active'>首页</NavItem>
          </Link>
          <NavItem className='left'>下载App</NavItem>
          {
            loginStatus ? (<NavItem className='right log' onClick={logout}>退出</NavItem>)
              : (
                <Link to="/login">
                  <NavItem className='right log'>登陆</NavItem>
                </Link>
              )
          }

          <NavItem className='right'>
            <i className='iconfont icon-Aa'></i>
          </NavItem>

          <SearchWrapper>
            <CSSTransition
              in={focused}
              timeout={300}
              classNames="slide"
            >
              <NavSearch className={focused ? 'focused' : ''}
                onFocus={() => handleInputFocus(list)}
                onBlur={handleInputBlur}
              ></NavSearch>
            </CSSTransition>
            <i className={focused ? 'focused iconfont icon-search zoom' : 'iconfont icon-search zoom'}></i>
            {this.getListArea()}
          </SearchWrapper>

        </Nav>
        <Addition>
          <Button className='reg'>注册</Button>
          <Link to="/write">
            <Button className='writting'>
              <i className="iconfont icon-weibiaoti1"></i> 写文章
          </Button>
          </Link>
        </Addition>
      </HeaderWrapper>
    )
  }
}
const mapStateToProps = state => {
  return {
    focused: state.getIn(['header', 'focused']),
    list: state.getIn(['header', 'list']),
    page: state.getIn(['header', 'page']),
    totalPage: state.getIn(['header', 'totalPage']),
    mouseIn: state.getIn(['header', 'mouseIn']),
    loginStatus: state.getIn(['login', 'login'])
  }
}
const mapDispathToProps = dispatch => {
  return {
    handleInputFocus(list) {
      //只获取一次列表数据
      list.size === 0 && dispatch(actionCreators.getList())
      dispatch(actionCreators.searchFocus())
    },
    handleInputBlur() {
      dispatch(actionCreators.searchBlur())
    },
    handleMouseEnter() {
      dispatch(actionCreators.mouseEnter())
    },
    handleMouseLeave() {
      dispatch(actionCreators.mouseLeave())
    },
    handleChangePage(page, totalPage, spin) {
      //把 spin的角度提取出来
      let originAngle = spin.style.transform.replace(/[^0-9]/gi, '')
      if (originAngle) {
        originAngle = parseInt(originAngle, 10)
      } else {
        originAngle = 0
      }
      spin.style.transform = `rotate(${originAngle + 360}deg)`
      if (page < totalPage) {
        dispatch(actionCreators.changePage(page + 1))
      } else {
        dispatch(actionCreators.changePage(1))
      }
    },
    //退出登录
    logout() {
      dispatch(loginActionCreators.logout())
    }
  }
}
export default connect(
  mapStateToProps,
  mapDispathToProps
)(Header)
