import styled from 'styled-components'

export const HomeWrapper = styled.div`
   width: 960px;
   margin: 0 auto;
   overflow: hidden;
`;
export const HomeLeft = styled.div`
   float: left; 
   padding-top: 30px;
   margin-left: 15px;  
   width: 625px;
    .banner-img {
       width: 625px;
       height: 270px;
    }
`;
export const HomeRight = styled.div`
  float: right;
  width: 280px;
`;
export const TopicWrapper = styled.div`
   overflow: hidden;
   margin-left: -18px;
   padding: 20px 0 10px 0;
	 border-bottom: 1px solid #dcdcdc;
`;
export const TopicItem = styled.div`
	float: left;
	margin-left: 18px;
	margin-bottom: 18px;
	padding-right: 10px;
	border: 1px solid #dcdcdc;
	border-radius: 4px;
	height: 32px;
	line-height: 32px;
	font-size: 14px;
	color: #000;
	background: #f7f7f7;
	.topic-pic {
		float: left;
		display: block;
		margin-right: 10px;
		width: 32px;
		height: 32px;
	}
`;

export const ListItem = styled.div`
	 overflow: hidden;
	 padding: 25px 0;
	 border-bottom: 1px solid #dcdcdc;
	  .pic {
			float: right;
			display: block;
			border-radius: 10px;
			width: 150px;
			height: 100px;
			}
`;
export const ItemInfo = styled.div`
		width: 458px;
		float: left;
		.title {
			margin: 0;
			line-height: 27px;
			font-size: 18px;
			font-weight: bold;
			color: #333;
		}
		.desc {
			line-height: 24px;
			font-size: 13px;
			color: #999;
		}
`;
export const RecommendList = styled.div`
  padding: 30px 0;
  min-height: 228px;
`;
export const RecommendItem = styled.div`
	width: 280px;
	height: 50px;
	margin-bottom: 8px;
	background: url(${props => props.imgUrl});
	background-size: contain;
	cursor: pointer;
`;
export const DownloadApp = styled.div`
  overflow: hidden;
  width: 100%;
	min-height: 50px;
	padding: 10px 20px;
	border: 1px solid #dcdcdc;
	border-radius: 4px;
	box-sizing: border-box;
	vertical-align: middle;
	cursor: pointer;
	 .download_app {
		 float: left;
		 display: inline-block;
		 margin-right: 8px;
		 width: 60px;
		 height: 60px;
	 }
	 .donwon_title {
		margin: 12px 0 0 0;
		font-size: 15px;
		color: #333;
	}
	 .description {
		font-size: 13px;
		color: #999;
	}
`;
export const WriterWrapper = styled.div`
	width: 100%;
	height: 295px;
`;

export const WriterWrapperTitle = styled.div`
	width: 100%;
	color: #969696;
	font-size: 14px; 
	overflow: hidden;
	 .title {
		display: inline-block;
    float: left;
	 }
	 .spin {
		 display: inline-block;
		 font-size: 14px;
		 margin-right: 5px;
	 }
	 .change {
		display: inline-block;
		float: right;
		cursor: pointer;
	 }
`;
export const WriterItem = styled.div`
  overflow: hidden;
	width: 100%;
	padding: 15px 10px;
	vertical-align: middle;
	.avatar {
		float: left;
		width: 48px;
		height: 48px;
		cursor: pointer;
		border-radius: 50%;
	}
	.avatarName {
		margin-left: 15px;
		font-size: 14px;
		text-decoration: none;
		color: #333;
	}
	.subcribe {
		float: right;
		margin-right: 12px;
		font-size: 13px;
		color: #42c02e;
		text-decoration: none;
		cursor: pointer;
	}
	.description {
		margin:2px 0 0 62px;
		font-size: 12px;
		color: #969696;
	}
`;
export const SeeMore = styled.div`
	 margin: 20px auto;
   width: 260px;
	 height: 35px;
	 text-align: center;
	 line-height: 35px;
	 border: 1px solid #dcdcdc;
	 border-radius: 6px;
	 box-sizing: border-box;
	 font-size: 13px;
	 color: #787878;
	 background-color: #f7f7f7;
	 cursor: pointer;
`;
export const FindMore = styled.div`
	width: 100%;
	height: 40px;
	line-height: 40px;
	margin: 30px 0;
	background: #a5a5a5;
	text-align: center;
	border-radius: 20px;
	color: #fff;
	cursor: pointer;
`;
export const ScrollTop = styled.div`
  position: fixed;
	right: 100px;
	bottom: 100px;
	width: 30px;
	height: 30px;
	cursor: pointer;
	 .backtop {
		 font-size: 30px;
	 }
`;