import styled from 'styled-components'

export const LoginWrapper = styled.div`
   position: absolute;
   left: 0;
   right: 0;
   bottom: 0;
   top: 66px;
   background: #ccc;
`;
export const LoginBox = styled.div`
  width: 250px;
  height: 180px;
  margin: 70px auto 0;
  padding: 30px 50px 30px;
  background: #fff;
  box-shadow: 0 0 8px rgba(0,0,0,.1);
  border-radius: 4px;
`;
export const Input = styled.input`
  display: block;
  margin: 10px auto 20px;
  width: 200px;
  height: 30px;
`;
export const Button = styled.div`
  margin: 20px auto;
  width: 200px;
  height: 30px;
  font-size: 15px;
  line-height: 30px;
  text-align: center;
  background: #3194d0;
  border-radius: 15px;
  color: #fff;
  cursor: pointer;
`