import React, { PureComponent } from 'react'
import { connect } from 'react-redux'
import {
  RecommendList,
  RecommendItem,
  DownloadApp
} from '../style'
class Recommend extends PureComponent {
  render() {
    const { list } = this.props;
    return (
      <div>
        { <RecommendList>
          {
            list.map(item => {
              return <RecommendItem key={item.get('id')} imgUrl={item.get('imgUrl')}></RecommendItem>
            })
          }
          <DownloadApp>
            <img src="EWM.png" alt="简书APP" className="download_app" />
            <div className="download_info">
              <p className="donwon_title" >下载简书手机App</p>
              <span className="description">随时随地发现和创作内容</span>
            </div>
          </DownloadApp>
        </RecommendList>}
      </div>
    )
  }
}
const mapState = (state) => ({
  list: state.getIn(['home', 'recommendList'])
})
export default connect(mapState)(Recommend);